import { USER_LOGIN_REQUEST, USER_LOGIN_FAIL, USER_LOGOUT, USER_LOGIN_SUCCESS, USER_REGISTER_FAIL, USER_REGISTER_REQUEST, USER_REGISTER_SUCCESS, USER_DETAILS_FAIL,USER_DETAILS_REQUEST,USER_DETAILS_SUCCESS, USER_UPDATE_PROFILE_REQUEST, USER_UPDATE_PROFILE_SUCCESS, USER_UPDATE_PROFILE_FAIL, USER_UPDATE_PROFILE_RESET, USER_DETAILS_RESET, USER_LIST_REQUEST, USER_LIST_SUCCESS, USER_LIST_FAIL, USER_LIST_RESET, USER_DELETE_SUCCESS, USER_DELETE_REQUEST, USER_DELETE_FAIL, USER_UPDATE_REQUEST, USER_UPDATE_SUCCESS, USER_UPDATE_FAIL} from "../constants/userConstants";
import axios from 'axios';

import { ORDER_LIST_MY_RESET } from "../constants/orderConstants";

export const login = (email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_LOGIN_REQUEST
        });

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const {data} = await axios.post('/api/users/login/', {'username':email, 'password':password}, config);

        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo', JSON.stringify(data))

    } catch(error){
        dispatch({type: USER_LOGIN_FAIL, payload: error.response && error.response.data.detail ? error.response.data.detail : error.message});
    }
}

export const logout = () => (dispatch) => {
    localStorage.removeItem('userInfo');
    dispatch({type: USER_LOGOUT});
    dispatch({type: USER_DETAILS_RESET});
    dispatch({type: ORDER_LIST_MY_RESET});
    dispatch({type: USER_LIST_RESET})
}

export const register = (name, email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_REGISTER_REQUEST
        });

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const {data} = await axios.post('/api/users/register/', {'name':name, 'email':email, 'password':password}, config);

        dispatch({
            type: USER_REGISTER_SUCCESS,
            payload: data
        })

        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo', JSON.stringify(data))

    } catch(error){
        dispatch({type: USER_REGISTER_FAIL, payload: error.response && error.response.data.detail ? error.response.data.detail : error.message});
    }
}


export const getUserDetails = (id) => async (dispatch) => {
    try {
        dispatch({
            type: USER_DETAILS_REQUEST
        });
        

        const userInfo = JSON.parse(localStorage.getItem('userInfo'));

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const {data} = await axios.get(`/api/users/${id}/`, config);

        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data
        })

    } catch(error){
        dispatch({type: USER_DETAILS_FAIL, payload: error.response && error.response.data.detail ? error.response.data.detail : error.message});
    }
}


export const updateUserProfile = (user) => async (dispatch) => {
    try {
        dispatch({
            type: USER_UPDATE_PROFILE_REQUEST
        });
        

        const userInfo = JSON.parse(localStorage.getItem('userInfo'));

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.token}`
            }
        }

        const {data} = await axios.put(`/api/users/profile/update/`, user,  config);

        dispatch({
            type: USER_UPDATE_PROFILE_SUCCESS,
            payload: data
        })

        dispatch({
            type: USER_LOGIN_SUCCESS,
            payload: data
        })

        localStorage.setItem('userInfo', JSON.stringify(data))

    } catch(error){
        dispatch({type: USER_UPDATE_PROFILE_FAIL, payload: error.response && error.response.data.detail ? error.response.data.detail : error.message});
    }
}



